import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ExchangeVoucherInvoiceComponent } from './exchange-voucher-invoice.component';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule } from '@angular/forms'; 
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailComponent } from './detail/detail.component';

const routes: Routes = [
  {
    path: '',
    component: ExchangeVoucherInvoiceComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  }
]



@NgModule({
  declarations: [
    ExchangeVoucherInvoiceComponent,DetailComponent
  ],
  imports: [
    
    CommonModule,
    RouterModule.forChild(routes),
    IonicModule,
    TranslateModule,
    FormsModule,
    NgZorroAntModule,
  ]
})
export class ExchangeVoucherInvoiceModule { }
