
import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhSaleExchangeVoucherInvoices, VhSaleExchangeVoucherInvoiceDetails } from 'src/app/sales/interface/vh-sale-voucher-invoice';
import { ExchangeVoucherK57Col3Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k57-col3/exchange-voucher-k57-col3.component';
import { ExchangeVoucherK80Col3Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k80-col3/exchange-voucher-k80-col3.component';
import { ExchangeVoucherK80Col4Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-k80-col4/exchange-voucher-k80-col4.component';
import { ExchangeVoucherA5Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-a5/exchange-voucher-a5.component';
import { ExchangeVoucherA4Component } from 'src/app/sales/components/print/exchange-voucher/exchange-voucher-a4/exchange-voucher-a4.component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhBillType41DetailEdit, VhBillType41Edit } from 'src/app/sales/components/bill-type/bill-type-41/bill-type41-interface';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent {


  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType41Edit;
  invoiceDetail : VhBillType41DetailEdit[]
  dataRestore: any;
  percent_discount: number = 0;
  bill_type: any;
  constructor(
    private router: Router,
    public vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    private nzModalService: NzModalService,

  ) {
    let data = this.router.getCurrentNavigation().extras.state
    this.bill_type = data.data.bill_type;
    this.dataRestore = data.dataRestore
    this.getDataInvoice(data.data._id)
    this.init(data.data.bill_type)
  }

  /**
   * gán bill và bill-detail get được vào biến và get các thông tin của nv, khách hàng, phương thức thanh toán
   * @example this.init(bill, bill-deails)
   */
  init(bill_type, invoice: VhBillType41Edit = null, invoiceDetail: VhBillType41DetailEdit[] = null) {
    if (bill_type == 41) this.invoice = invoice
    this.invoiceDetail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQuerySales.getlocalCustomer(this.invoice.id_customer),
        
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }

  /**
   * get dữ liệu bill từ DB
   * @param id : nhận từ state truyền qua
   * @example this.getDataInvoice('DHIWHNXDIWODUHIWDHOWID')
   */
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        this.init(invoice.bill_type, invoice, invoiceDetail);
        this.vhComponent.hideLoading(0)
      })
    })
  }
  /**
   * trả về tên unit của sản phẩm theo ratio truyền vào
   * @param units 
   * @param ratio 
   * @returns string
   * @example let unit_name = this.getUnit([object], 1)
   */
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }
  /**
   * trả về chuỗi thông tin lô theo id lô truyền vào
   * @param lots 
   * @param _id 
   * @returns string
   * @example let lot = this.getLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
 
  ngAfterViewChecked() {
    if (document.querySelector("#sales-return-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-tabs-nav") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#sales-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#sales-return-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }

    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/voucher/exchange-voucher-invoice'], { state: this.dataRestore });
  }

  /**
   * xóa hóa đơn lên DB
   * @example this.deleteInvoice()
   */
  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("sell_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel this invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQuerySales.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.alertMessageDesktop("success",
                  `${this.languageService.translate("Hóa đơn đổi voucher")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`)
                this.router.navigate(['/sales/dashboard/manage/sales-program/voucher/exchange-voucher-invoice'], { state: { ...this.dataRestore, reload: true } })
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.alertMessageDesktop("error", this.languageService.translate('You do not have this rights'))
  }

  printInvoice() {
    // let print = this.vhQuerySales.getLocalPrintPage('page_desktop_exchange_voucher')
    // if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
    //   let printer =  print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)]

    // }

    this.checkPrint().then((printer: any) => {
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer, invoice: this.invoice ,  invoice_detail : this.invoiceDetail}, false, false, `modal-print-${type}`).then((modal) => {
          modal.onWillDismiss().then(() => this.goBack())
        })
      }
    }, (err) => {
      this.nzModalService.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => { this.gotoPrinter('page_desktop_exchange_voucher', 'Đổi voucher lấy hàng') }
      })

    })
  }

  gotoPrinter(type, name) {
    this.router.navigate(['/sales/dashboard/manage/printer', type], { state: name });
  }
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  private checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_exchange_voucher')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects(false)
    })

  }


  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExchangeVoucherK57Col3Component
      case 'print_size_k80_3c': return ExchangeVoucherK80Col3Component
      case 'print_size_k80_4c': return ExchangeVoucherK80Col4Component
      case 'print_size_a5': return ExchangeVoucherA5Component
      case 'print_size_a4': return ExchangeVoucherA4Component
      default: return ExchangeVoucherA4Component
    }
  }


  /**
   * trả về chuỗi thông tin địa chỉ của khách hàng
   */
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
 
}
